'use client';

import { Button } from '@/components/ui/Button';
import { useCart } from '@/context/CartContext';
import { useState } from 'react';

// Mock data fetcher to simulate getting a single product
const getProduct = (id: string) => {
    const MOCK_DETAILS = {
        id,
        name: 'MagSafe Wireless Charger',
        price: 29.99,
        category: 'Chargers',
        description: 'Experience optimal charging with our premium MagSafe Wireless Charger. Designed for efficiency and style, it snaps perfectly onto your iPhone 14/15 series for faster wireless charging up to 15W.',
        images: [
            'https://images.unsplash.com/photo-1625591340248-6d2894ebd784?q=80&w=1000&auto=format&fit=crop',
            'https://images.unsplash.com/photo-1585338107529-13afc5f02586?q=80&w=1000&auto=format&fit=crop'
        ],
        features: [
            'Magnetic alignment',
            'Up to 15W fast charging',
            'USB-C integrated cable',
            'Premium minimal build'
        ]
    };
    return MOCK_DETAILS;
};

export default function ProductPage({ params }: { params: { id: string } }) {
    const product = getProduct(params.id);
    const { addItem } = useCart();
    const [isAdding, setIsAdding] = useState(false);

    const handleAddToCart = () => {
        setIsAdding(true);
        addItem({
            id: product.id,
            name: product.name,
            price: product.price,
            image: product.images[0]
        });
        setTimeout(() => setIsAdding(false), 500);
    };

    return (
        <div className="af-main-container" style={{ paddingTop: '40px', paddingBottom: '80px' }}>
            <section className="af-section">
                <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))', gap: '60px' }}>

                    {/* Gallery */}
                    <div className="flex flex-col gap-4">
                        <div style={{ aspectRatio: '1', borderRadius: '24px', overflow: 'hidden', background: '#f5f5f7' }}>
                            <img src={product.images[0]} alt={product.name} style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                            {product.images.slice(1).map((img, i) => (
                                <div key={i} style={{ aspectRatio: '1', borderRadius: '16px', overflow: 'hidden', background: '#f5f5f7' }}>
                                    <img src={img} alt="" style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
                                </div>
                            ))}
                        </div>
                    </div>

                    {/* Details */}
                    <div style={{ padding: '20px 0' }}>
                        <div style={{ fontSize: '0.85rem', color: 'var(--af-gray-text)', fontWeight: 700, textTransform: 'uppercase', marginBottom: '12px', letterSpacing: '0.1em' }}>
                            {product.category}
                        </div>
                        <h1 style={{ fontSize: '3rem', fontWeight: 800, marginBottom: '24px', lineHeight: 1.1 }}>{product.name}</h1>
                        <p style={{ fontSize: '2rem', fontWeight: 500, marginBottom: '32px' }}>${product.price.toFixed(2)}</p>

                        <div style={{ display: 'flex', gap: '16px', marginBottom: '40px' }}>
                            <button
                                onClick={handleAddToCart}
                                disabled={isAdding}
                                style={{
                                    flex: 1,
                                    padding: '18px',
                                    backgroundColor: isAdding ? '#333' : '#000',
                                    color: '#fff',
                                    borderRadius: '50px',
                                    fontWeight: 600,
                                    fontSize: '1rem',
                                    border: 'none',
                                    cursor: 'pointer',
                                    transition: 'background-color 0.2s'
                                }}>
                                {isAdding ? 'Added' : 'Add to Cart'}
                            </button>
                            <button style={{
                                width: '60px',
                                borderRadius: '50%',
                                border: '1px solid #e5e5e5',
                                display: 'flex', alignItems: 'center', justifyContent: 'center'
                            }}>
                                â™¡
                            </button>
                        </div>

                        <div style={{ borderTop: '1px solid #f0f0f0', paddingTop: '32px' }}>
                            <h3 style={{ fontSize: '1.2rem', fontWeight: 600, marginBottom: '16px' }}>Overview</h3>
                            <p style={{ lineHeight: 1.7, color: '#444', marginBottom: '24px' }}>
                                {product.description}
                            </p>
                            <ul style={{ listStyle: 'none', padding: 0 }}>
                                {product.features.map((feature, i) => (
                                    <li key={i} style={{ display: 'flex', alignItems: 'center', gap: '10px', marginBottom: '12px', color: '#444' }}>
                                        <span style={{ width: '6px', height: '6px', backgroundColor: '#000', borderRadius: '50%' }}></span>
                                        {feature}
                                    </li>
                                ))}
                            </ul>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    );
}
